import { Table, TableBody, TableCell, TableHead, TableRow } from "@material-ui/core";
import React from "react";
import { IApartment } from "../Interfaces";

interface IProps {
  apartments: IApartment[];
}

export const ApartmentTable = ({ apartments }: IProps) => {
  console.log(apartments);

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell align="left">Adresse</TableCell>
          <TableCell align="left">Größe</TableCell>
          <TableCell align="left">Räume</TableCell>
          <TableCell align="left">Miete</TableCell>
          <TableCell align="left">Eigenmittel</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
          {apartments.map((apartment, index) => (
            <TableRow key={index}>
              <TableCell align="left">
                {apartment.address}
              </TableCell>
              <TableCell align="left">{apartment.size}</TableCell>
              <TableCell align="left">{apartment.rooms}</TableCell>
              <TableCell align="left">{apartment.rent}</TableCell>
              <TableCell align="left">{apartment.deposit}</TableCell>
            </TableRow>
          ))}
        </TableBody>
    </Table>
  );
};
